




























































import topLevelDomains from '../data/tld.js'
export default
	name: 'search'
	data: ->
		query: ''
		root_domains: [
			'ru'
			'su'
			'net'
			'com'
			'org'
			'tech'
			'io'
			'pro'
		],
		topLevelDomains: topLevelDomains
		placeholders: {
			"search": "Type to search",
			"settings": "Type to filter"
		}
		result: {}
		timer: null
		loading: false
		showSettings: false
		settingsRegexEnabled: false
	computed: 
		tlds: ->
			@topLevelDomains.filter (tld)=> 
				if @settingsRegexEnabled 
					regex = new RegExp("^#{@query}")
					regex.test(tld)
				else if @query.length > 1
					tld == @query
				else
					true
			.sort @sortTlds
	created: ->
		if localStorage && root_domains = JSON.parse(localStorage.getItem('rootDomains'))
			@root_domains = root_domains
	mounted: ->
		window.addEventListener 'keydown', (e)=>
			@query='' if e.key == 'Escape'
		@$refs.query.focus()
	methods:
		getFullDomain: (domain)-> [@query,domain].join('.')
		check: (domain)->	@result[@getFullDomain(domain)]
		getLink: (domain) -> if @check(domain) then "https://www.reg.ru/choose/domain/?domains=#{@getFullDomain(domain)}" else "https://www.reg.ru/whois/?dname=#{@getFullDomain(domain)}"
		toggleSettings: ->
			@query = ''
			@showSettings = !@showSettings
		checkTLD: (tld) -> @root_domains.indexOf(tld) != -1
		toggleTLD: (tld) ->
			if @root_domains.indexOf(tld) != -1
				@root_domains = @root_domains.filter((domain)=> domain != tld)
			else
				@root_domains.push(tld)
				@root_domains = @root_domains.sort()
			@topLevelDomains.sort @sortTlds
			@$forceUpdate()
			localStorage.setItem('rootDomains',JSON.stringify(@root_domains)) if localStorage
		sortTlds: (a,b)->
			if @root_domains.indexOf(a) != -1
				-1
			else 
				1
		search: ->
			unless @showSettings
				return if @query.length < 3
				clearTimeout(@timer) if @timer
				@timer = setTimeout ()=>
					@result = {}
					@loading = true
					axios.get '/api/whois',
						params:
							query: @query
							domains: @root_domains.join(',')
					.then ({data}) =>
						@result = data
						@loading = false
					.catch (response) =>
						@loading = false
				, 500
		filter: (e)->
			unless @showSettings
				e.preventDefault() unless e.key.match /[\.\w_-]{1,1}/ 
			@query = @query.toLowerCase()
		



